@import url(https://fonts.googleapis.com/css?family=Merienda);
@import url(https://fonts.googleapis.com/css?family=Caveat+Brush);
@import url(https://fonts.googleapis.com/css?family=Cormorant+SC:700);
@import url(https://fonts.googleapis.com/css?family=Cormorant+SC:700|Inknut+Antiqua);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
    font-family: 'rust';
    src: url(/static/media/rustico-regular.257090ac.woff2) format('woff2'),
         url(/static/media/rustico-regular.a8c54204.woff) format('woff');
    font-weight: normal;
    font-style: normal;

}
/* INTRO PAGE */
.intro{
    height:100vh;
    width: 100%;
    margin: auto;
    background-image: url(/static/media/snowboard.1f8e5887.jpg);
    display: table;
    top: 0;
    background-size: cover;
}
.intro .inner {
    display: table-cell;
    vertical-align: middle;
    width: 100%;
    max-width: none;
}

.content {
    max-width: 500px;
    margin: 0 auto;
    text-align: center;
}

.content h1 {
    font-family: 'rust';
    color: black;
    font-size: 700%;
    text-align: center;

    -webkit-animation: fadein 10s;
            animation: fadein 10s;
}

.intro-btn  {
    border-radius: 10px;
    font-family: 'rust';
    color: black;
    font-size: 300%;
    padding: 10px 20px;
    border: solid rgba(255, 255, 255, 0.445) 3px;
    text-transform: uppercase;
    -webkit-box-decoration-break: none;
            box-decoration-break: none;
    -webkit-animation: fadein 25s;
            animation: fadein 25s;

}
.intro-btn:hover {
    color: #ffffff;
    border: solid #ffffff 3px;
}
@-webkit-keyframes fadein {
    from { opacity: 0;}
    to   { opacity: 1;}
}
@keyframes fadein {
    from { opacity: 0;}
    to   { opacity: 1;}
}

@media (max-width: 768px){
    .intro{
        background-size:cover, 100vh 100vw;
        
    }
    .content h1{
        font-size: 300%;
    }
    .intro-btn  {
        font-size: 200%; 
}
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

